/* form elements end */
/* user options */
.user-options {
  position: relative;
  display: flex;
  border-radius: 0 0 7px 7px;
  background-color: white;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
}

.user-options__item {
  position: relative;
  height: 4.3rem;
  min-width: 5.4rem;
}

.no-flexwrap .user-options__item {
  display: inline-block;
  vertical-align: top;
}

.user-options__item::before, .user-options__item::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  height: 1.8rem;
  width: 0.1rem;
  margin: -0.9rem 0;
  background-color: #979797;
  opacity: 0.2;
}

.user-options__item::before {
  left: 0;
}

.user-options__item::after {
  right: 0;
}

.user-options__item:first-child::before, .user-options__item:last-child::after {
  display: none;
}

.user-options__item > a, .user-options__item > div {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 5px 2rem;
  color: #131313;
  text-decoration: none;
  text-align: center;
}

.touchevents .user-options__item > a, .touchevents .user-options__item > div {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.user-options__item > a[data-title]::before, .user-options__item > div[data-title]::before {
  content: attr(data-title);
  display: block;
  min-width: 100%;
  padding: 0.4rem 0.8rem;
  position: absolute;
  left: 0;
  top: 100%;
  margin-top: 2px;
  font-size: 1.2rem;
  line-height: 2rem;
  text-align: center;
  letter-spacing: normal;
  white-space: nowrap;
  z-index: 2;
  color: #262626;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, -2px);
  transform: translate3d(0, -2px, 0);
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  will-change: transform, opacity, visibility;
  transition-property: transform, opacity, visibility;
  transition-delay: 0s;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.user-options__item > a svg, .user-options__item > div svg {
  display: inline-block;
  vertical-align: middle;
  width: 1.6rem;
  height: 1.6rem;
}

.user-options__item > a svg ~ span, .user-options__item > div svg ~ span {
  padding-left: 12px;
}

@media (max-width: 639px) {
  .user-options__item > a svg ~ span, .user-options__item > div svg ~ span {
    display: none;
  }
}

.user-options__item > a svg ~ span.hide-text, .user-options__item > div svg ~ span.hide-text {
  padding-left: 0;
}

.user-options__item > a path, .user-options__item > div path {
  fill: #ee6e73;
}

.user-options__item > a > span, .user-options__item > div > span {
  display: inline-block;
  vertical-align: middle;
  font-size: 1.1rem;
  line-height: 1.3rem;
  letter-spacing: normal;
  text-align: left;
}

.user-options__item > a .counter, .user-options__item > div .counter {
  display: block;
  position: absolute;
  right: 50%;
  top: 50%;
  background-color: #e61f1f;
  width: 1rem;
  height: 1rem;
  margin: -0.8rem -1.5rem;
  padding: 0.1rem 0 0 0.1rem;
  font-style: normal;
  font-size: 0.9rem;
  line-height: 1rem;
  text-align: center;
  color: white;
  border-radius: 0.5rem;
  z-index: 10;
}

.user-options__item:first-child > a, .user-options__item:first-child > div {
  border-bottom-left-radius: 7px;
}

.user-options__item:last-child > a, .user-options__item:last-child > div {
  border-bottom-right-radius: 7px;
}

.user-options__item > div {
  cursor: default;
}

.user-options__item:last-child > a[data-title]::before, .user-options__item:last-child > div[data-title]::before {
  left: auto;
  right: 0;
}

.no-touchevents .user-options__item > a[data-title]:hover::before, .no-touchevents .user-options__item > div[data-title]:hover::before {
  transition-delay: 0.6s;
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
  transform: translate3d(0, 0, 0);
}

.user-options__item > a {
  transition-property: color, box-shadow, background-color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.user-options__item > a path {
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.user-options__item > a:hover {
  background-color: #fff;
}

